.smallsidebar, .slidebar {
  height: 100%;
  width: 200px;
  transition: 0.5 ease;
  cursor: pointer;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  color: var(--background-color);
  z-index: 1;
}

.bigsidebar {
  width: 50px;
  height: 90vh;
  transition: 0.5 ease;
  background-color: var(--primary-color);
  cursor: pointer;
  color: var(--background-color);
}

.menu-items {
  color: var(--background-color);
  text-decoration: none;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  gap: 1rem;
  padding: 10px;

}

.menu-items.logout {
  position: absolute;
  bottom: 10px;
}


.active-menu {
  color: var(--text-color);
  background: var(--background-color);
}

.active-menu svg {
  color:var(--text-color);
}

.bigsidebar .menu-items {
  margin-bottom: 20px;
}

.logo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: block;
  position: relative;
  margin: 20px 50px;
  box-shadow: 0 5px 35px var(--text-color);
  border: 5px solid var(secondary-color);
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}



/* Responsive Design */
@media screen and (max-width: 768px) {
  .smallsidebar, .slidebar {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 10vh;
    display: flex;
    flex-direction: row;
    gap:calc(100% / 20);
    .logo {
      display: none;
    }
    .menu-text {
      display: none;
    }
    .menu-items.logout {
      position: relative;
      bottom: 0;
    }
    svg{
        font-size: 2.5rem;
    }
  }
}